//Main_ css
#mainVisualSlider{
    background-color: aliceblue;
    width: 100%;
    height: auto;
    aspect-ratio: 1920 / 747;
    .slick-slider{
        width: 100%;
        height: 100%;
        position: relative;
        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2;
            background-color: unset;
            border: 0;
            font-size: 0;
        }
        button.slick-prev{left: 5rem;background: url(../public/main_prev.png)no-repeat center center/cover;width: 3.3rem;height: 6.2rem;}
        button.slick-next{right: 5rem;background: url(../public/main_next.png)no-repeat center center/cover;width: 3.3rem;height: 6.2rem;}
        .slick-list{
            height:100%;
            .slick-track{
                height: 100%;
                 div{
                    height: 
                    100%;
                .mainVisualImg{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    background:linear-gradient(rgba(36, 36, 36, 0.527), rgba(36, 36, 36, 0.5)), url(../src/media/main.jpg);background-repeat: no-repeat;background-size: cover;
                    display: flex!important;
                    flex-wrap: wrap;justify-content: center;align-items: center;
                    .mainVisualInner {
                    max-width: 144rem;
                    width: 100%;
                    height: max-content;
                    h3{
                        font-size: 7.1rem;
                        font-weight: 800;
                        color: #fff;
                        width: 48.7rem;
                        line-height: 1.2;
                        margin-bottom: 6rem;
                    }
                    p{
                        font-size: 3.1rem;
                        color: #fff;
                        line-height: 1.1;
                        width: 53rem;
                        font-weight: 100;
                    }
                    }
                }
                .slider01{background:linear-gradient(rgba(36, 36, 36, 0.527), rgba(36, 36, 36, 0.5)), url(../src/media/main.jpg);background-repeat: no-repeat;background-size: cover;}
                }
                
            }
        }
    }
}
#middleCont{
    padding: 16rem 0;
    .inner{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(34rem, 1fr));
        gap: 2.4rem;
        width: 100%;
        .cardWrap{
            padding-bottom: 3rem;
            box-shadow: 0.1rem 0.7rem 1.4rem rgba(37, 37, 37, 0.3);
            overflow: hidden;
            border-radius: .6rem;
            min-height: 48.3rem;
            position: relative;
            figure{
                width:100%;
                height: 192px;
                /* aspect-ratio: 340 / 192; */
                overflow: hidden;
                img{
                    object-fit: cover;
                }
            }
            dl{
                padding-top: 3rem;
                padding-left: 3rem;
                padding-right: 3rem;      
                dt{
                    font-size: 3.1rem;
                    font-weight: 700;
                    color: #1c1d1c;
                    line-height: 1.2;
                    margin-bottom: 1.6rem;
                }
                dd{
                    font-size: 1.8rem;
                    line-height: 1.5;
                    color: #424242;
                }
                dd:last-child{
                    font-size: 1em;
                    color: #bdbdbd;
                    position: absolute;
                    bottom: 3rem;
                }
            }
        }
    }


}
#lastCont{
    background-color: #f5f5f5;
    min-height: 60rem;
    #customCont{
        height: 100%;
        min-height: inherit;
        display: flex;
        flex-wrap: wrap;
        .customcont{
            height: 100%;
            min-height: inherit;
        }
        .leftArea{
            flex: 3;
            background-color: #0f5337;
            .inner{
                max-width: unset;
                min-height: inherit;
                padding-left:12vw;
                display: flex;
                flex-wrap: wrap;
                flex-direction: column;
                justify-content: center;
            }
        }
        .rightArea{
            flex: 5;
        }
    }
}